import React from 'react';
import {CLink, CRow, CCol} from '@coreui/react';

export default ()=>{
  /*example start: template*/
  return (
    <div>
      <CRow>
        <CCol lg>
          <CLink
            href="https://coreui.io"
            target="_blank"
          >
            Normal external link.
          </CLink>
        </CCol>
        <CCol lg>
        </CCol>
        <CCol lg>
          <CLink disabled>
            Disabled link without action
          </CLink>
        </CCol>
        <CCol lg>
          <CLink
            className="text-white bg-info px-2"
            href="https://coreui.io"
            target="_blank"
          >
            Custom link.
          </CLink>
        </CCol>
      </CRow>
    </div>
  )
  /*example end: template*/
}
