import React from 'react';
import {CForm, CFormGroup, CInputGroup, CInputGroupPrepend, CInputGroupText, CInput, CInputGroupAppend} from '@coreui/react';
import {CIcon} from '@coreui/icons-react';
import {cilUser} from '@coreui/icons';
React.icons = {cilUser}

export default ()=>{
  /*example start: template*/
  return (
    <div className={'mt-2 col-6'}>
      <CForm>
        <CFormGroup>
          <CInputGroup>
            <CInputGroupPrepend>
              <CInputGroupText>Username</CInputGroupText>
            </CInputGroupPrepend>
            <CInput type="email" id="username3" name="username3" autoComplete="name"/>
            <CInputGroupAppend>
              <CInputGroupText><CIcon name={'cilUser'}/></CInputGroupText>
            </CInputGroupAppend>
          </CInputGroup>
        </CFormGroup>
      </CForm>
    </div>
  )
  /*example end: template*/
}
