import React from 'react';
import {CProgress, CProgressBar} from '@coreui/react';

export default ()=>{
  /*example start: script*/
  let counter = 86;
  /*example end: script*/
  /*example start: template*/
  return (
    <div className={'mt-2'}>
      <CProgress
        value={counter}
        animated
        showValue
        height="30px"
      />
      <CProgress className="mt-1">
        <CProgressBar
          showValue
          value={counter*(6/10)+3}
          color="success"
          height="30px"
        />
        <CProgressBar
          showValue
          value={counter*(2.5/10)+3}
          color="warning"
        />
        <CProgressBar
          showValue
          value={counter*(1.5/10)+3}
          color="danger"
        />
      </CProgress>
    </div>
  )
  /*example end: template*/
}
