import React from 'react';
import {CButton, CRow} from '@coreui/react';

export default ()=>{
  /*example start: template*/
  return (
    <div>

      Sizes:

      {['sm', '', 'lg'].map((size, rowKey)=>{
        return (
          <CRow key={rowKey + 'sizes'} className="d-flex justify-content-between">
            {
              ['success', 'primary', 'info', 'warning', 'danger'].map((color, key)=>{
                return (
                  <CButton
                  key={color + size}
                  color={color}
                  size={size}
                  className="m-2"
                >
                  {color}
                </CButton>
              )})
            }
          </CRow>
        )})}

        Styles:

        {[{ shape: 'square' }, { shape: 'pill' }, { variant: 'ghost' }, { variant: 'outline' }].map((style, rowKey)=>{
          return (
            <CRow key={rowKey + 'sizes'} className="d-flex justify-content-between">
              {
                ['success', 'primary', 'info', 'warning', 'danger'].map((color, key)=>{
                  return (
                    <CButton
                    key={key}
                    color={color}
                    {...style}
                    className="m-2"
                  >
                    {color}
                  </CButton>
                )})
              }
            </CRow>
          )})}

    </div>
  )
  /*example end: template*/
}
