import React from 'react';
import {CInput, CFormGroup, CLabel} from '@coreui/react';

export default ()=>{
  /*example start: template*/
  return (
    <div>
      <CFormGroup>
        <CLabel htmlFor="name">Name</CLabel>
        <CInput id="name" placeholder="Enter your name" required />
      </CFormGroup>
    </div>
  )
  /*example end: template*/
}
