/*example start: template*/

import React, { useState } from "react";
import {CButton, CCollapse} from '@coreui/react';

export default () => {

  const [open, toggleCollapse] = useState(false)

  return (
    <>
      <CButton
        className="m-0 p-0"
        color="link"
        onClick={() => toggleCollapse(!open)}
      >
        Toggle item
      </CButton>
      <CCollapse show={open}>
        <p className="mb-3">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit.
        </p>
      </CCollapse>
    </>
  )
}
/*example end: template*/