import React from 'react';
import {CLink, CSwitch} from '@coreui/react';

export default ()=>{

  /*example start: script*/
  const modes = [
        {},
        { size: 'sm'},
        { size: 'lg'},
        { variant: '3d' },
        { shape: 'pill' },
        { variant: 'opposite' },
        { variant: 'outline', shape:'pill' }
      ];
  const colors = ['primary', 'success', 'warning', 'danger', 'info', 'dark', 'light', 'secondary'];
  /*example end: script*/

  /*example start: template*/
  return (
    <div>
      {modes.map((mode, index)=>{
        return (
          <div className="d-flex justify-content-between my-4" key={index}>
            Passed props: {JSON.stringify(mode)}
            {
              colors.map((color, key)=>{
                return (
                  <CSwitch
                    key={key}
                    color={color}
                    checked
                    value={color}
                    {...mode}
                  />
                )
              })
            }
          </div>
        )
      })}
    </div>
  )
  /*example end: template*/

  /*
  <template v-for="(mode, index) in modes">
    Passed props: {{mode}}
    <div class="d-flex justify-content-between my-4" :key="index">
      <CSwitch
        :key="key"
        :color="color"
        v-bind="mode"
        checked
        :value="color"
        v-for="(color, key) in colors"
      />
    </div>
  </template>
  */

}
